.container-cards {
    align-items: center;
    background-image: url(../images/bg-main-mobile.png);
    background-size: cover;
    display: flex;
    flex-direction: column;
    height: 200px;
    justify-content: center;
    margin-bottom: 6rem;
    width: 100%;

    .container-cards__card-back,
    .container-cards__card-front {
        border-radius: 1rem;
        color: hsl(0, 0%, 100%);

        img.front,
        img.back {
            border-radius: .5rem;
            box-shadow: 0 0 3px #222;
            width: 280px;
        }
    }

    .container-cards__card-back {
        position: relative;
        right: -1.5rem;
        top: 5rem;
        z-index: 0;

        p {
            right: 2rem;
            position: absolute;
            top: 4rem;
        }
    }

    .container-cards__card-front {
        left: -1.8rem;
        position: relative;
        top: .5rem;

        img.bandeira {
            position: absolute;
            height: 50px;
            right: 1rem;
            top: .2rem;
            max-width: 40px;

            &[scr="mastercard"] {
                width: 10px;
            }
        }

        .data-card-front {
            display: grid;
            grid-template-rows: 55% 30% 0%;
            height: 100%;
            letter-spacing: 1px;
            padding: 1rem;
            position: absolute;
            top: 0;
            width: 100%;

            .card-front__circle {
                background-color: hsl(0, 0%, 100%);
                border: 1px solid hsl(0, 0%, 100%);
                border-radius: 100%;
                height: 30px;
                position: relative;
                width: 30px;
        
                &::after {
                    border: 1px solid hsl(0, 0%, 100%);
                    border-radius: 100%;
                    content: '';
                    height: 15px;
                    position: absolute;
                    right: -1.7rem;
                    top: .4rem;
                    width: 15px;
                }
            }
        
            .card-front__number-card {
                font-size: 1.2rem;
                letter-spacing: 1px;
                justify-self: center;
                text-transform: uppercase;
            }
        
            .card-front__data-usercard {
                display: flex;
                justify-content: space-around;
                padding-inline: .5rem;

                p {
                    font-size: .7rem;
                    letter-spacing: .5px;
                    text-transform: uppercase;
                }
        
            }
        }
    }

}

// Desktop
@media(min-width: 1100px) {
    .container-cards {
        background-image: url(../images/bg-main-desktop.png);
        height: 100%;
        position: relative;
        width: 340px;

        .container-cards__card-back,
        .container-cards__card-front {
            img.front {
                width: 305px;
            }
        }   

        .container-cards__card-back {
            right: -10.5rem;
            top: 11rem;

            p {
                position: absolute;
                right: 2.2rem;
                top: 4.05rem;
            }
        }

        .container-cards__card-front {
            left: 6.4rem;
            position: relative;
            top: -11rem;

            .data-card-front {
                width: 305px;

                .card-front__number-card {
                    font-size: 1.4rem;
                }
            }
        }

    }
}