.container-form {
    display: flex;
    height: 100%;
    justify-content: center;
    padding-inline: 1.5rem;
    width: 100%;

    .form-card {
        display: flex;
        flex-direction: column;
        gap: 1.8rem;
        max-width: 500px;
    
        input, button {
            border: 1px solid hsl(270, 3%, 87%);
            border-radius: .5rem;
            font-size: 1rem;
            font-weight: 400;
            letter-spacing: .7px;
            outline: none;
            padding: .7rem;
            transition: .2s;
        }

        label {
            color: hsl(278, 68%, 11%);
            font-weight: bold;
            letter-spacing: .5px;
            position: relative;

            span {
                bottom: 2px;
                font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
                font-size: .6rem;
                right: 2px;
                position: absolute;
            }
        }


        input {
            color: hsl(278, 68%, 11%);
            text-transform: uppercase;

            &:focus {
                box-shadow: 
                inset 1px 0 0 hsl(249, 99%, 64%),
                inset 0 1px 0 hsl(249, 99%, 64%),
                inset -1px 0 0 hsl(278, 94%, 30%),
                inset 0 -1px 0 hsl(278, 94%, 30%);

            }
        } 

        button {

            &:hover, &:active {
                background-color: hsl(278, 68%, 15%);
                cursor: pointer;
            }
        }
    
        .form-card__campos {
            display: flex;
            flex-direction: column;
            gap: .4rem;
            width: 100%;
        }
    
        .form-card__campo-exp {
            display: grid;
            gap: .8rem;
            grid-template-columns: 50% auto;
    
            .infos-card,
            .cvc {
                display: flex;
                flex-direction: column;
                gap: .5rem;
    
                input {
                    width: 100%;
                }
            }
    
            .input-exp {
                display: flex;
                gap: .5rem;
    
                input {
                    width: 50%;
                }
            }
        }

        button {
            background-color: hsl(278, 68%, 11%);
            color: hsl(0, 0%, 100%);
        }
    }
}

// box-shadow: 
// inset 1px 1px 1.5px hsl(0, 100%, 66%),
// inset -1px -1px 1.5px hsl(0, 100%, 66%);

// Errors
.form-card__campos,
.form-card__campo-exp,
.cvc,
.infos-card {
    position: relative;

    .errors {
        animation: 3s ease;
        bottom: -1.2rem;
        color: hsl(0, 100%, 66%);
        display: none;
        font-size: .8rem;
        left: 0;
        position: absolute;
        transition: 1s;
    }

    .error-date-exp-yy {
        left: 8.3rem;
    }
}

@media(max-width: 540px) {
    .container-form {
        .form-card {
            justify-content: center;
            max-width: 340px;
        }

        .infos-card {
            .error-date-exp-yy {
                left: 6rem;
            }
        }
    }
}

@media(min-width: 1100px) {
    .container-form {
        .form-card {
            justify-content: center;
            max-width: 340px;
        }

        .infos-card {
            .error-date-exp-yy {
                left: 5.8rem;
            }
        }
    }
}