.container-completed {
    align-items: center;
    animation: input-animation 1.5s forwards ease-in;
    display: flex;
    justify-content: center;
    margin-top: 5rem;
    position: relative;
    width: 100%;

    @keyframes input-animation {
        from {
            display: none;
            opacity: 0;
        } to {
            display: bock;
            opacity: 1;
        }
    }

    .container-completed__father {
        max-width: 500px;

        .container-completed__message {
            display: flex;
            flex-direction: column;
            gap: 1rem;
            text-align: center;
    
            img {
                margin: 0 auto;
                width: 50%;
            }
    
            h2 {
                color: hsl(278, 68%, 11%);
                margin-top: 1rem;
            }
            
            p {
                color: hsl(279, 6%, 55%);
                font-weight: 500;
                margin-bottom: 1rem;
            }
    
            button {
                background-color: hsl(278, 68%, 15%);
                border: 1px solid transparent;
                border-radius: .5rem;
                color: hsl(0, 0%, 100%);
                font-size: 1rem;
                font-weight: 500;
                letter-spacing: .7px;
                padding: .7rem;
    
                &:hover, &:active {
                    background-color: hsl(278, 68%, 11%);
                    cursor: pointer;
                }
            }
        }
    }
}

@media(min-width: 1100px) {
    .container-completed {
        margin: 0;
    }
}