.container-principal {
    background-color: hsl(0, 0%, 98%);
    display: flex;
    flex-direction: column;
    font-family: 'Space Grotesk', sans-serif;
    height: 100vh;
    max-width: 100vw;
}

@media(min-width: 1100px){
    .container-principal {
        flex-direction: row;
    }
}
